.book {
  position: relative;
}

.book_contents {
  display: flex;
  justify-content: center;
  /* border: 1px solid white; */
}
.book_name {
  color: white;
  font-size: 50px;
  padding: 10px;
  margin-top: 20px;
  position: absolute;
  top: 220px;
}
.book_image {
  margin: 10px 0px 10px 0px;
  padding: 30px;
  background-color: #1b1b1b;
  border-radius: 10px;
  width: 82%;
  height: 478px;
}

.book_contents {
  padding: 20px;
  display: flex;
  align-items: flex-start;
}

.book_name {
  margin-top: 10px;
  margin-left: 10px;
}
.banner_icon {
  position: absolute;
  right: 20px;
  top: 10px;
  font-size: 27px;
}
.banner_side > .-MuiSvgIcon-root {
  font-size: 27px;
}
.date {
  position: absolute;
  font-size: 18px;
  top: 300px;
  color: rgb(179, 32, 13);
  padding: 4px;
  margin-left: 25px;
  background-color: rgb(224, 224, 188);
  border-bottom-right-radius: 8px;
  border-bottom-left-radius: 8px;
}
.banner_overview {
  position: absolute;
  width: 300px;
  top: 330px;
  margin-top: 10px;
  width: 45rem;
  line-height: 1.3;
  max-width: 360px;
  height: 80px;

  margin-left: 25px;
}

.banner_right > h3 {
  color: #fff;
  margin-left: 19px;
}
.banner_right > img {
  height: 100px;
  object-fit: cover;
  margin-left: 15px;
  width: 100px;
}
